import React, { useState } from "react";
import { projectDetails } from "../Details";
import ProjectCard from "./ProjectCard";
import "react-multi-carousel/lib/styles.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { motion, AnimatePresence } from 'framer-motion';

function Projects() {
  const [itemsInView, setItemsInView] = useState(3);
  const [activeSlide, setActiveSlide] = useState(0);

  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1200, min: 800 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 800, min: 0 },
      items: 1,
    },
  };

  const handleResponsiveChange = (newResponsive) => {
    setItemsInView(newResponsive.desktop.items || 1);
  };

  const settings = {
    infinite: true,
    slidesToShow: itemsInView,
    slidesToScroll: 1,
    arrows: false,
    beforeChange: (oldIndex, newIndex) => setActiveSlide(newIndex),
  };

  return (
    <div className="relative h-[100vh] overflow-hidden  bg-black">
      <AnimatePresence >
        <motion.div
          key={activeSlide} 
          style={{
            backgroundImage: `url(${projectDetails[activeSlide].image})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}
          className="w-full h-[100vh]"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{ duration: 1 }}
        >
          <div className="absolute top-0 left-20 h-full w-1/3 flex flex-col justify-center font-primary">
            <motion.p
              key={projectDetails[activeSlide].title} 
              className="text-white font-bold text-6xl w-full mb-12"
              initial={{ opacity: 0, x: -200, scale: 0.8 }} 
              animate={{ opacity: 1, x: 0, scale: 1 }}
              transition={{ duration: 0.5 }}
            >
              {projectDetails[activeSlide].title}
            </motion.p>

            <motion.p
              key={projectDetails[activeSlide].description}
              className="text-white bg-black bg-opacity-50 rounded-lg text-xl w-full font-Rubik"
              initial={{ opacity: 0, x: -100}} 
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 1 }}
            >
              {projectDetails[activeSlide].description}
            </motion.p>

            <motion.p
              key={projectDetails[activeSlide].githubLink}
              initial={{ opacity: 0, x: 0, scale: 0 }} 
              animate={{ opacity: 1, x: 0, scale: 1 }}
              transition={{ duration: 1 }}
              whileHover={{
                scale: 1.2,
                transition: { duration: 1 },
              }}
              className="cursor-pointer text-white font-bold text-xl w-40 text-center border-2 rounded-2xl p-4 mt-10 bg-gradient-to-tr from-purple-600 to-red-500"
            >
              GitHub
            </motion.p>
          </div>
        </motion.div>
      </AnimatePresence>

      <div className="absolute top-0 right-0 content-center h-[70vh] w-[60vw] mt-80">
        <Slider className="h-full" {...settings}>
          {projectDetails.map(
            ({ title, image, concepts, description, githubLink }, index) => (
              <div
                key={title}
                className={`my-20 ml-10 h-full p-12 transition-transform duration-300 ${
                  index === activeSlide ? 'scale-125' : ''
                }`}
              >
                <ProjectCard
                  title={title}
                  image={image}
                  concepts={concepts}
                  description={description}
                  githubLink={githubLink}
                  isActive={index === activeSlide}
                />
              </div>
            )
          )}
        </Slider>
      </div>
      <div className="flex transition-all duration-1000  flex-row gap-2 w-full items-center justify-center absolute bottom-0 p-4">
        {projectDetails.map((element, index) => 
          <div key={index} className={`w-4 h-4 rounded-full bg-red-800 ${
            index === activeSlide ? 'animate-bounce bg-[#ee5236]' : ''
          }`}></div>
        )}
      </div>
    </div>
  );
}

export default Projects;
